import React, { useState, useEffect } from 'react';
import Slider from "react-slick";
import GetQuoteButton from '../getQuoteButton';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import './index.css';

const HeroSection = () => {
    const isMobile = window.innerWidth < 768;
    const initialSettings = {
        dots: isMobile ? false : true,  // No dots on mobile
        infinite: true,
        speed: 2000,
        slidesToShow: isMobile ? 1 : 3,  // Show 1 slide on mobile, 3 otherwise
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 6000,
    };
    const [sliderSettings, setSliderSettings] = useState(initialSettings);

    useEffect(() => {
        const handleResize = () => {
            // Check if mobile inside resize handler
            const isMobile = window.innerWidth < 768;
            setSliderSettings({
                ...sliderSettings,
                dots: !isMobile,  // Toggle dots based on size
                slidesToShow: isMobile ? 1 : 3,  // Toggle number of slides
            });
        };

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    return (
        <section className="Hero">
            <Slider {...sliderSettings}>
                <div>
                    <img src="/sliderimage1.png" alt="Homeowner's Insurance" style={{ width: '384px', height: '384px' }} />
                </div>
                <div>
                    <img src="/sliderimage2.png" alt="Renter's Insurance" style={{ width: '384px', height: '384px' }} />
                </div>
                <div>
                    <img src="/sliderimage4.png" alt="Smart Devices" style={{ width: '384px', height: '384px' }} />
                </div>
                <div>
                    <img src="/sliderimage5.png" alt="Drones" style={{ width: '384px', height: '384px' }} />
                </div>
            </Slider>
            <h2>Comprehensive Insurance Solutions for Everyone</h2>
            <p>Providing tailored insurance policies to protect what matters most to you.</p>
            <GetQuoteButton />
        </section>
    );
  };

  export default HeroSection;