import React, { useState } from 'react';
import Modal from 'react-modal';
import emailjs from 'emailjs-com';
import './index.css';

Modal.setAppElement('#root'); // Make sure to bind modal to your appElement (https://reactcommunity.org/react-modal/accessibility/)

const LeadFormModal = ({ isOpen, onRequestClose }) => {
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        phone: '',
        message: '',
        bestTime: ''
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value
        });
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        // Handle form submission, e.g., send data to your API
        console.log(formData);
        sendEmail(formData);
        onRequestClose(); // Close the modal after submission
    };

    const sendEmail = (data) => {
        const serviceID = 'service_f33vu9c';
        const templateID = 'template_h12x6zn';
        const userID = 'maVvIeALJ9D2zGjt4';

        emailjs.send(serviceID, templateID, data, userID)
            .then(response => {
                console.log('Email sent successfully:', response.status, response.text);
            })
            .catch(error => {
                console.error('Error sending email:', error);
            });
    };

    return (
        <Modal
            isOpen={isOpen}
            onRequestClose={onRequestClose}
            contentLabel="Get a Quote"
            className="modal"
            overlayClassName="overlay"
        >
            <h2>Get a Quote</h2>
            <form onSubmit={handleSubmit}>
                <div>
                    <label>Name:</label>
                    <input
                        type="text"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div>
                    <label>Email:</label>
                    <input
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                    />
                </div>
                <div>
                    <label>Phone:</label>
                    <input
                        type="tel"
                        name="phone"
                        value={formData.phone}
                        onChange={handleChange}
                    />
                </div>
                <div>
                    <label>Best Time to Call:</label>
                    <input
                        type="text"
                        name="bestTime"
                        value={formData.bestTime}
                        onChange={handleChange}
                    />
                </div>
                <div>
                    <label>What kind of insurance are you looking for?</label>
                    <input
                        name="message"
                        value={formData.message}
                        onChange={handleChange}
                        required
                    />
                </div>
                <button type="submit">Submit</button>
                <button type="button" onClick={onRequestClose}>Close</button>
            </form>
        </Modal>
    );
};

export default LeadFormModal;
