import logo from '../../logo.svg';
import GetQuoteButton from '../getQuoteButton';
import './index.css';

const Header = () => {
    return (
      <header className="App-header">
        <img src={logo} alt="Arch Insurance Logo" style={{ width: '150px' }} />
        <nav>
          <ul>
            <li><GetQuoteButton /></li>
          </ul>
        </nav>
      </header>
    );
  };

  export default Header;